	module.exports = " <div class=\"container faq\"> <h2>一例一休中所刪除的七天國定假日週休二日的勞工早就沒有放了，刪除七天假的爭議點是？</h2> <div class=answer> <p> 過去實施雙週 84 小時工時，若勞工每個平常日（週一到週五）都工作八小時，雙週工時為 80 小時，所以每週還少上兩個小時的正常工時。勞動部主動解釋因為勞工尚未做滿法定工時，一年下來欠 104 個小時，所以可以合法挪移，讓七天假日免放。 </p> <p> 但是現已實施單週 40 小時工時，若沒有調整勞基法的狀況，依照現行法令週休二日的勞工也享有這七天假期，並非「早就沒有放這七天假期」。 </p> <p> 參考資料： <ul> <li><a href=\"https://youtu.be/4gd2m_73NHE?t=23m13s\">有話好說：勞工絕食 召委落跑！一例一休闖關失敗！ 23 分 13 秒處</a></li> <li><a href=\"http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030002&FLNO=23\">勞基法施行細則 23 條</a></li> </ul> </p> </div> <h2>新增更多常見問答或修改現有問答</h2> <div class=answer> 若想新增或修改常見問答，請登入 <a href=https://github.com/g0v/workweek/issues>github 並且開新 issue</a>。 </div> </div> ";

